import * as Plotly from 'plotly.js';
import {Axis, PlotData} from 'plotly.js';
import React, {MouseEvent, useEffect, useRef, useState} from 'react';
import Button from 'react-bootstrap/Button';
import ButtonGroup from 'react-bootstrap/ButtonGroup';
import {FaRulerVertical, FaRulerHorizontal} from 'react-icons/fa';
import Plot, {Figure} from 'react-plotly.js';
import {Region} from '../../AppConfig';
import useWindowWidth from '../../hooks/resize/useWindowWidth';
import Airtime, {CodingRate} from '../../lora/Airtime';
import HelpTooltip from '../help/HelpTooltip';

type GraphProps = {
  region: Region;
  selectedPacketSize: number;
  codingRate: CodingRate;
};

/**
 * A graph showing time on air for all the region's data rates (SF, bandwidth)
 * and their supported full LoRaWAN payload sizes.
 *
 * The graph resets the visible traces when the region or coding rate changes,
 * but tries to preserve pan, zoom and scale. Changing the window width will
 * reset any panning, but keeps the choice for the range of the x-axis.
 */
export default function Graph({region, selectedPacketSize, codingRate}: GraphProps) {
  const windowWidth = useWindowWidth();

  // https://github.com/plotly/react-plotly.js/#state-management suggests one
  // can keep the state
  // const [figure, setFigure] = useState<Figure>({data: [], layout: {}, frames: []});

  // If not set, then Plotly will set default for autorange upon first render
  const initialFigureRef = {data: [], layout: {xaxis: {autorange: false}}, frames: []};
  const figureRef = useRef<Figure>(initialFigureRef);
  const stateRef = useRef({
    dataVisible: [] as (PlotData['visible'] | undefined)[],
    lastWindowWidth: -1,
    // xaxisFullRange: false,
    xaxis: {
      autoRange: false as Axis['autorange'] | undefined,
      range: undefined as Axis['range'] | undefined,
    },
    // yaxisLogarithmic: false,
  });
  const [revision, setRevision] = useState(0);
  const [xaxisFitFullRange, setXaxisFitFullRange] = useState(false);
  const [yaxisLogarithmic, setYaxisLogarithmic] = useState(false);

  console.log('RENDER; FIGUREREF ', JSON.parse(JSON.stringify(figureRef.current)));

  /**
   * Toggle the range of the x-axis between using a fixed scale, and fitting the
   * full graph.
   */
  const toggleFitFullRange = (e: MouseEvent) => {
    const fitFullRange = !xaxisFitFullRange;
    stateRef.current.xaxis.autoRange = fitFullRange;
    if (!fitFullRange) {
      stateRef.current.xaxis.range = undefined;
    }
    console.log('TOGGLE ZOOM', fitFullRange);
    setXaxisFitFullRange(fitFullRange);
  };

  /**
   * Toggle the y-axis between linear and logarithmic scale.
   */
  const toggleScale = (e: MouseEvent) => {
    setYaxisLogarithmic((current) => !current);
  };

  /**
   * Save the parts of the full chart state that have been set by user
   * interaction, such as hiding traces from the chart, or pan/zoom.
   */
  const updateFigure = (figure: Readonly<Figure>) => {
    if (codingRate) {
      console.log('UPDATE FIGURE parsed ', JSON.parse(JSON.stringify(figure)), codingRate);
      figureRef.current = figure;

      const autorange = figureRef.current.layout.xaxis?.autorange;
      const range = figureRef.current.layout.xaxis?.range;
      if (range && range[0] < 0 && figureRef.current.layout.xaxis) {
        // For auto-range, Plotly adds some padding within the graph. Remove the
        // left padding. There will also be some padding on the right (about the
        // same size as the one on the left), but keep that to make clear that
        // the full graph is visible.
        // For manual panning beyond 0, add it for otherwise the range gets smaller and smaller.
        figureRef.current.layout.xaxis.range = [0, range[1] - (autorange ? 0 : range[0])];
        figureRef.current.layout.xaxis.autorange = false;
        setRevision((current) => ++current);
        console.log('FIX RANGE', range[0], figureRef.current.layout.xaxis.range);
      }

      stateRef.current.dataVisible = figure.data.map((d) => d.visible);
      // layout.xaxis.autorange is cleared on manual pan/zoom
      stateRef.current.xaxis.autoRange = figure.layout.xaxis?.autorange;

      // TODO validate comment
      // When updating `revision` to force Plotly to redraw the graph (mostly to
      // force the legend to reset the expected traces) then Plotly invokes
      // `onUpdate` with only `{data: [], layout: {xaxis: {autorange: false}}}`.
      // For that, preserve the previous range:
      if (figure.layout.xaxis?.range) {
        stateRef.current.xaxis.range = figure.layout.xaxis?.range;
      }
      console.log('BUTTON REF parsed', JSON.parse(JSON.stringify(stateRef.current)));
    }
  };

  const onUpdate = (figure: Readonly<Figure>) => {
    console.log('ON UPDATE', JSON.parse(JSON.stringify(figure)));
    updateFigure(figure);
  };

  // Typings expect event: Readonly<Plotly.ButtonClickEvent> but it's
  // {active: number, button: {}, menu

  // Note that clicking a button does not make Plotly fire any other event (it
  // only fires onButtonClicked; not onUpdate, onRelayout nor onRestyle). So, we
  // don't know what new range Plotly has calculated.
  // const onButtonClicked = (event: any) => {
  //   console.log('BUTTON CLICKED', event);
  //   switch (event.button.name) {
  //     case 'toggleScale':
  //       stateRef.current.yaxisLogarithmic = event.active === -1;
  //       break;
  //     case 'toggleRange':
  //       // buttonRef.current.xaxisAutoRange = event.active === -1;
  //       // buttonRef.current.xaxis.autoRange = event.active === -1;
  //       stateRef.current.xaxisFullRange = event.active === -1;
  //       break;
  //     default:
  //     // none
  //   }
  //   console.log(stateRef.current);
  // };

  // const onRelayout = (event: Readonly<Plotly.PlotRelayoutEvent>) => {
  //   console.log('RELAYOUT', event);
  // };
  //
  // const onRestyle = (event: Readonly<Plotly.PlotRestyleEvent>) => {
  //   console.log('RESTYLE', event);
  // };

  useEffect(() => {
    console.log('EFFECT [region, codingRate]');
    // Restore the disabled traces
    // updateFigure(initialFigureRef);
    // initFigure();
    stateRef.current.dataVisible = [];
    // We need to make Plotly draw the plot again to enable the data rates. This
    // also makes it reset the range though.
    setRevision((current) => ++current);
  }, [region, codingRate]);

  useEffect(() => {
    console.log('EFFECT [selectedPacketSize, windowWidth, buttonRef.current.xaxisFullRange]');
    // Restore the disabled traces
    // figureRef.current = initialFigureRef;
    // initFigure();
    // }, [selectedPacketSize, windowWidth, xaxisFullRange, yaxisLogarithmic]);
  }, [selectedPacketSize, windowWidth]);

  if (!codingRate) {
    console.log('NO CODING RATE');
    return null;
  }

  // const initFigure = () => {
  //   if (!codingRate) {
  //     return;
  //   }
  //   console.log('INIT FIGURE');
  //
  const data = region.dataRates.map(
    (dr, idx): Plotly.Data => {
      // PHYPayload = MHDR[1] | MACPayload[..] | MIC[4]
      const maxPhyPayloadSize = dr.maxMacPayloadSize + 5;
      const maxApplicationPayloadSize = maxPhyPayloadSize - 13;

      // We could also use D3:
      // const sizes = Plotly.d3.range(0, maxPhyPayloadSize + 1);
      const sizes = Array.from(Array(maxPhyPayloadSize + 1).keys());

      const values = sizes.map((size) => {
        const airtime = Airtime.calculate(size, dr.sf, dr.bw, codingRate);
        return {
          y: airtime,
          // text: airtime.toFixed(1) + ' ms',
          size: size === selectedPacketSize ? 8 : 4,
        };
      });

      // const visible =
      //   figureRef.current.data[idx]?.visible ?? (dr.highlight === 'low' ? 'legendonly' : true);
      const visible =
        stateRef.current.dataVisible[idx] ?? (dr.highlight === 'low' ? 'legendonly' : true);
      console.log('VISIBLE', dr.name, stateRef.current.dataVisible[idx], visible);

      return {
        x: sizes,
        y: values.map((val) => val.y),
        // text: values.map((val) => val.text),
        // See https://plotly.com/javascript/reference/#scatter-hovertemplate
        hovertemplate: '%{y:,.1f} ms<extra>%{x} bytes on %{fullData.name}</extra>',
        marker: {
          size: values.map((val) => val.size),
          // symbol: 'triangle-up',
        },
        type: 'scatter',
        mode: 'lines+markers',
        // An error in the typings doesn't allow for 'x+text+name'
        hoverinfo: 'x+text+name' as any,
        // textinfo: 'label+text',
        visible: visible,
        name: `${dr.name} SF${dr.sf}BW${dr.bw}`,
        connectgaps: true,
        line: {
          shape: 'hvh',
          dash: 'dot',
          width: 0.5,
        },
      };
    }
  );
  // 970 - 658 = 312
  const xaxisFixedScaleRange = [0, (windowWidth - 312) / 6];
  const yaxisLinearTitle = 'airtime (ms)';
  const yaxisLogarithmicTitle = 'airtime (ms, logarithmic)';

  // TODO document
  // layout.uirevision only keeps zoom interaction, not disabling traces.

  // TODO autorange is cleared when user pans graph
  const xaxisAutorange = stateRef.current.xaxis.autoRange;
  // When resizing the window, we want the new default range without a page
  // refresh. This will reset any panning.
  console.log('RANGE', stateRef.current);
  const xaxisRange =
    xaxisFitFullRange || stateRef.current.lastWindowWidth === windowWidth
      ? stateRef.current.xaxis?.range ?? xaxisFixedScaleRange
      : xaxisFixedScaleRange;
  // const xaxisRange = xaxisAutorange
  //   ? buttonRef.current.xaxis?.range ?? xaxisDefaultRange
  //   : xaxisDefaultRange;
  stateRef.current.lastWindowWidth = windowWidth;

  // const xaxisAutorange = buttonRef.current.xaxisAutoRange;
  // const xaxisRange = xaxisAutorange
  //   ? figureRef.current.layout.xaxis?.range ?? xaxisDefaultRange
  //   : xaxisDefaultRange;
  const yaxisType = yaxisLogarithmic ? 'log' : 'linear';
  const yaxisTitle = yaxisLogarithmic ? yaxisLogarithmicTitle : yaxisLinearTitle;

  // const xaxisAutorange = figureRef.current.layout.xaxis?.autorange ?? false;
  // const xaxisRange = xaxisAutorange
  //   ? figureRef.current.layout.xaxis?.range ?? xaxisDefaultRange
  //   : xaxisDefaultRange;
  // const yaxisType = figureRef.current.layout.yaxis?.type ?? 'linear';
  // const yaxisTitle = figureRef.current.layout.yaxis?.title ?? yaxisLinearTitle;

  // console.log('AUTORANGE', figureRef.current.layout.xaxis?.autorange, xaxisAutorange);

  const layout: Partial<Plotly.Layout> = {
    autosize: true,
    // autosize: false,
    // margin: {l: 0, t: 0, r: 0, b: 0},
    margin: {l: 70, t: 0, r: 5},
    // height: 600,
    dragmode: 'pan',
    // title: `Airtime for data rate and application payload size${
    //   codingRate === '4/5' ? '' : ` CR${codingRate}`
    // }`,
    xaxis: {
      autorange: xaxisAutorange,
      range: xaxisRange,
      title: 'total packet size (overhead + application payload)',
      ticks: 'outside',
      fixedrange: xaxisFitFullRange,
      // automargin: true,
      // rangemode: 'tozero',
    },
    yaxis: {
      type: yaxisType,
      title: yaxisTitle,
      ticks: 'outside',
      // automargin: true,
      autorange: true,
      fixedrange: true,
      rangemode: 'tozero',
      separatethousands: true,
    },
    legend: {
      orientation: 'h',
      y: -0.2,
      // orientation: windowWidth < 600 ? 'h' : 'v',
      // y: windowWidth < 600 ? -0.2 : 1,
      xanchor: 'center',
      x: 0.5,
      // Reverse to make the position in the legend match the order in the graph
      // traceorder: windowWidth < 600 ? 'reversed' : 'normal',
    },
    // Legend inside the graph
    // legend: {
    //   x: 1,
    //   xanchor: 'right',
    //   y: 1,
    // },
    // See https://plotly.com/javascript/reference/layout/updatemenus/
    // See https://plotly.com/javascript/plotlyjs-function-reference/#plotlyupdate
    // See https://plotly.com/javascript/plotlyjs-function-reference/#plotlyrelayout
    // The term attribute strings is used above to mean flattened (e.g., {marker: {color: 'red'}} vs. {'marker.color': red}). When you pass an attribute string to restyle inside the update object, its assumed to mean update only this attribute. Therefore, if you wish to replace and entire sub-object, you may simply specify one less level of nesting.
    // updatemenus: [
    //   {
    //     buttons: [
    //       {
    //         name: 'toggleScale',
    //         label: 'toggle scale',
    //         // update(traceUpdate: Partial<PlotData>, layoutUpdate: Partial<Layout>)
    //         // When specifying hierarchical string values, such as
    //         // `{'yaxis.type': 'log'}`, only those values will be changed;
    //         // when specifying an object, like `{yaxis: {type: 'log'}}`
    //         // all non-defined attributes will be reset to their defaults.
    //         method: 'update',
    //         args: [{}, {'yaxis.type': 'linear', 'yaxis.title': yaxisLinearTitle}],
    //         args2: [{}, {'yaxis.type': 'log', 'yaxis.title': yaxisLogarithmicTitle}],
    //       },
    //     ],
    //     active: buttonRef.current.yaxisLogarithmic ? -1 : 0,
    //     direction: 'down',
    //     pad: {r: 10, t: 10},
    //     showactive: false,
    //     type: 'buttons',
    //     x: 1.1,
    //     xanchor: 'left',
    //     y: 0.1,
    //     yanchor: 'bottom',
    //   },
    //   {
    //     buttons: [
    //       {
    //         name: 'toggleRange',
    //         label: 'toggle zoom',
    //         method: 'update',
    //         args: [{}, {'xaxis.autorange': false, 'xaxis.range': xaxisDefaultRange}],
    //         args2: [{}, {'xaxis.autorange': true /*, 'xaxis.rangemode': 'tozero'*/}],
    //       },
    //     ],
    //     // active: buttonRef.current.xaxisAutoRange ? -1 : 0,
    //     active: buttonRef.current.xaxisFullRange ? -1 : 0,
    //     direction: 'down',
    //     pad: {r: 10, t: 10},
    //     showactive: false,
    //     type: 'buttons',
    //     x: 1.1,
    //     xanchor: 'left',
    //     y: 0,
    //     yanchor: 'bottom',
    //   },
    // ],
  };

  //   updateFigure({
  //     data: data,
  //     layout: layout,
  //     frames: null,
  //   });
  // };
  //
  // initFigure();

  // Even when using useLayoutEffect to postpone actually showing the Plotly
  // figure on the screen, Plotly will update the reference passed to its
  // attributes, especially `layout`. (It would also invoke `onInitialized`.)
  return (
    <>
      <Plot
        style={{width: '100%', height: '400px'}}
        revision={revision}
        useResizeHandler={true}
        // data={figureRef.current.data.length ? figureRef.current.data : traces.reverse()}
        // data={figureRef.current.data}
        data={data}
        // layout={{...layout, ...figureRef.current.layout}}
        // As Plot will modify this, and set some defaults, pass a copy
        // layout={{...figureRef.current.layout}}
        layout={layout}
        // layout={figureRef.current.layout}
        // frames={figure.frames}
        // onInitialized={(figure) => updateFigure(figure)}
        // Save user interaction, such as drag or disabling traces
        // TODO is not invoked for the buttons!
        // This is basically only needed for data[x].visible, and also triggered for pan/zoom
        onUpdate={onUpdate}
        // onButtonClicked={onButtonClicked}
        // onRelayout={onRelayout}
        // onRestyle={onRestyle}
        config={{
          // This also needs width: 100% for the <Plot> component
          responsive: true,
          displayModeBar: false,
          showEditInChartStudio: false,
          showTips: false,
          // showLink: true,
          // linkText: 'analyze chart',
          // plotlyServerURL: 'https://chart-studio.plotly.com',
        }}
      />
      <ButtonGroup>
        <HelpTooltip
          content={<>Toggle the vertical axis between a linear and logarithmic scale.</>}
        >
          <Button variant="outline-secondary" aria-label="Share" onClick={toggleScale}>
            <FaRulerVertical size="1em" />
            &nbsp;set vertical scale
          </Button>
        </HelpTooltip>
        &nbsp;
        <HelpTooltip
          content={
            <>
              Compress the horizontal range to include all allowed payload sizes, or use a
              scrollable range with a fixed-width scale per payload size.
            </>
          }
        >
          <Button variant="outline-secondary" aria-label="Copy" onClick={toggleFitFullRange}>
            <FaRulerHorizontal size="1em" />
            &nbsp;set horizontal range
          </Button>
        </HelpTooltip>
      </ButtonGroup>
      <br />
    </>
  );
}
